<template>
  <ul class="program-list" v-if="hasLoadData">
      <!--v-infinite-scroll="loadMore"-->
      <!--infinite-scroll-disabled="loading"-->
      <!--infinite-scroll-distance="10"-->
  <!--&gt;-->
    <li v-for="(item,index) in items" class="program-list__li"  >
      <ul class="program-list__detail-list">
        <li class="program-list__title">{{item.projectName}} <span class="Id">ID: {{item.projectID}}</span>
          <span class="participant">{{item.projectParticipated}}</span>
          <span class="help"  @click="goDetail(item.projectID)"></span>
          <span class="route"  @click="cashRoute(item.projectID)">?</span>

        </li>
        <li class="program-list__detail-list__li"><span class="money">{{item.projectTarget}}</span></li>
        <li class="program-list__detail-list__li">
          <span class="program-list__detail-list__span"></span>
          <mt-progress :value="item.projectcompleted"
                       :bar-height="8" style="height: 1rem;"></mt-progress>
          <!--echarts,canvas-->
          <!--<chart :chart-val="item.projectcompleted"></chart>-->
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
import chart from './chart'
import axios from 'axios'
import 'mint-ui'

export default {
  title: 'list',
  data: function(){
    return {
      chartVal : 70,
      items:[],
      hasLoadData: false,
      completed:[]
    }
  },
  props:[
    "parent"
  ],
  components: { chart },
  methods:{
    goDetail:function (num) {
      this.$router.push({name: 'Detail', query: {projectID: num}});
    },
    getList:function () {
      var that = this;
      var target = "";
      if(that.parent==="Home") {
        target = "/home"
      } else {
        target = "/engageList"
      }
      axios.get(target)
        .then(function (response) {
          console.log(response);
          if(response.data===""){
            console.log("yes");
          } else {
            console.log("not null");
          }
          let data = response.data;
          console.log(data);
          if(data!==null) {
            let len = data.length;
            if(len>9){
              len = 9;
            }
            if (that.items.length > 0) {

              for (let i = 0; i < len; i++) {
                that.items.pop();
              }
            }
            for (let i = 0; i < len; i++) {
              that.items.push(data[i]);
            }
            that.hasLoadData = true;
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    cashRoute: function (id) {
      this.$router.push({name:"Cashroute",query:{projectID: id}})
    }
  },
  mounted:function () {
    this.getList();
  },
  created: function () {
    this.hasLoadData = false;
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
  .program-list{
    /*width: calc( 100% - 40px );*/
    margin: auto;
    list-style: none;

    .program-list__li{
      font-family: "PingFang SC";
      height: 3rem;
      margin-bottom: 0px;
      background: #ffffff;
      padding: 0.5rem;
      /*background: url('./../../assets/logo_02.png') #fff no-repeat top right;*/
      background-size: auto 60%;
      /*border-radius: 10px;*/
      /*box-shadow: 0px 2px 2px #e8e8e8;*/
      border-bottom:1px solid #f2f2f2;


      .program-list__detail-list{
        height: 3rem;
        .program-list__title{
          position: relative;
          /*display: inline-block;*/
          width: 100%;
          text-align: left;
          font-size: 0.5rem;
          list-style: none;
          margin-bottom: 10px;
          font-weight: 600;
          color: dodgerblue;
          .Id{
            font-size: 14px;
            color: #666666;
          }
          .help{
            text-align: center;
            display: inline-block;
            height: 30px;
            width: 30px;
            color: white;
            background: #20a1ff;
            border-radius: 50%;
            position: absolute;
            right: 0;
            box-shadow: 0px 2px 10px #d2d2d2;

          }
          .route{
            z-index: 2;
            text-align: center;
            display: inline-block;
            height: 30px;
            width: 30px;
            color: white;
            background: #ff7200;
            border-radius: 50%;
            position: absolute;
            right: 0px;
            top: 43px;
            box-shadow: 0px 2px 10px #d2d2d2;
          }
          .participant{

            position: absolute;
            right: 40px;
            top: 5px;
            color: white;
            border-radius: 2px;
            text-align: center;
            font-family: 'Microsoft JhengHei UI';
            font-size: 16px;
            /*height: 30px;*/
            /*line-height: 30px;*/
            width: 40px;
            display: inline-block;
            background: #00d784;
            box-shadow: 0px 2px 5px #e9e9e9;


          }
        }
        .program-list__detail-list__li{
          list-style: none;
          font-size: 0.4rem;
          text-align: left;
          position: relative;
          text-indent: 0.5rem;
          height: 1rem;
          line-height: 1rem;

          .money{
            font-family: "Century Gothic";
            color: dodgerblue;
            font-size: 20px;
            font-weight: 600;
            position: relative;
            z-index: 1;
          }

          &::before{
            content: '';
            display: inline-block;
            height: 0.3rem;
            width: 0.3rem;
            background: #cad6e3;
            border-radius: 0.15rem;
            margin: auto;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
          }

          .program-list__detail-list__span{
            float: left;
          }

          .chart{
            width: 2rem;
            height: 2rem;
            float: left;
            margin-top: -0.5rem;
          }
        }
      }
    }
  }
</style>



