<template>
  <div class="chart"></div>
</template>

<script>
import echarts from 'echarts';

export default {
  data: function(){
    return {
      option : {
        series: [
          {
            name: '',
            type: 'gauge',
            splitNumber: 'null',
            axisLine: {            // 
              show: true,        // show
              lineStyle: {       // lineStyle
                color: [[1, '#6d8794']],
                width: 3
              }
            },
            axisTick: {
              show: false,
            },
            pointer : {
              length : '80%',
              width : 3,
              color : 'auto'
            },
            detail : {
              formatter:'{value}%',
              fontFamily: 'Century Gothic',
              fontSize:'',
              width: 100,
              color: '#6d8794',
              rich: {}
            },
            data: [{value: this.chartVal, name: ''}]
          }
        ]
      }
    }
  },
  props: [ 'chartVal' ],
  mounted: function(){
    var chartsArr = document.getElementsByClassName('chart');
    for( let i = 0; i < chartsArr.length; i++ ){
      var myChart = echarts.init(chartsArr[i]);
      myChart.setOption(this.option);
    }
  }
}
</script>

<style>
  .chart{
    height: auto;
  }
</style>



