<script>
import FooterComponent from './common-components/footer'
import chart from './common-components/chart'
import MtField from "../../node_modules/mint-ui/packages/field/src/field.vue";
import $ from 'jquery'

export default {
  name: 'detail',
  data () {
    return {
      linkActived: '/detail',
      chartVal: 70,
      hasSearch: false,
      items:[],
      showSubmit: false,
      detail:"",
      reviews:[]
    }
  },
  watch: {
    '$route' (to, from) {
      console.log("!");
      this.queryDetail();
      this.queryReview();
    }
  },
  created: function(){
    this.queryDetail();
    this.queryReview();
  },
  methods: {
    redirect:function(e){
      this.$router.push({name:'Purchase',query:{ProjectReceiver:this.items[0].projectStarter,ProjectID:this.items[0].projectID}})
    },
    queryDetail: function(){
      var that = this;
      let projectID = this.$route.query.projectID;
      console.log(projectID);
      this.axios.get('/detailPage?id='+projectID).then(function(res){
        console.log(res);
        that.hasSearch = (res.status===200);
        for(var i=0;i<1;i++){
          that.items.pop();
        }
        for(var i=0;i<1;i++){
          that.items.push(res.data[0]);
        }
      })
    },
    queryReview: function () {
      var that = this;
      let projectID = this.$route.query.projectID;
      this.axios.get('/review?projectId='+projectID).then(function(res){
        console.log(res);
        for(var i=0;i<that.reviews.length; i++){
          that.reviews.pop();
        }
        for(var i=0;i<res.data.length;i++){
          that.reviews.push(res.data[i]);
        }
      }).catch(function (err) {
        console.log(err.response.data);
      })
    },
    submitReview: function () {
      var that = this;
      let projectID = this.$route.query.projectID;
      this.axios.post('/review',{
        projectId: projectID,
        detail: that.detail,
        reviewDate: new Date().toLocaleDateString(),
        floor: 1,
      }).then(function(res){
        console.log(res.data);
        if(res.status===200){
          that.reviews.push(res.data);
          console.log(that.reviews[0]);
          $('#writeReview').slideToggle()
          that.showSubmit = !that.showSubmit
          that.detail = ""
        }
      }).catch(function (err) {
        console.log("!");
        console.log(err.response.data);
      })
    },
    slide:function () {
      var that = this;
      $('#writeReview').slideToggle()
      this.showSubmit = !this.showSubmit
    }
  },
  components: {
    MtField,
    FooterComponent, chart }
}
</script>

