<template>
  <div class="newProject">
    <!--<home-swiper class="home__swiper" ref="swiper"></home-swiper>-->
    <div class="formImg">
      <h1></h1>
    </div>
    <mt-popup
      v-model="popVisible"
      position="top"
      :modal=false
      class="popUp"
    >{{popInfo}}</mt-popup>
    <form action="http://localhost:8000/project" method="post" role="form" enctype="multipart/form-data" id="myform" ref="forms">
      <mt-popup
        v-model="popupVisible"
        position="top"
        style="background-color:rgba(255,255,255,0); color: white; margin-top: 30vh">
        <i class="fa fa-2x fa-times"></i> !
      </mt-popup>
      <mt-field label="" type="text" v-model="projectName" placeholder=""></mt-field>
      <mt-field label="" type="file" @change="handleFile()"  placeholder="" class="specFile">
        <span class="fileInfo">{{fileInfo}}</span>
        <!--<span class="upLdFile">+</span>-->
      </mt-field>
      <mt-cell title="" class="center_wrapper">
        <span class="setLeft">{{startValue}}</span>
        <button @click.stop.prevent="openPicker(1)" class="putCenter"><span class="plus">+</span></button>
      </mt-cell>
      <mt-datetime-picker  ref="startPicker"
                           type="date"
                           v-model="startDate"
                           yearFormat="{value} "
                           monthFormat="{value} "
                           dateFormat="{value} "
                           :startDate = "minDate"
                           @confirm="handleStartChange()">

      </mt-datetime-picker>
      <mt-cell title="" class="center_wrapper">
        <span class="setLeft">{{endValue}}</span>
        <button @click.stop.prevent="openPicker(2)" class="putCenter"><span class="plus">+</span></button></mt-cell>
      <mt-datetime-picker  ref="endPicker"
                           type="date"
                           yearFormat="{value} "
                           monthFormat="{value} "
                           dateFormat="{value} "
                           :startDate = "minDate"
                           v-model="endDate"
                           @confirm="handleEndChange()">
      </mt-datetime-picker>
      <mt-field type="text" name="targetCost" v-model="targetCost" label="" placeholder=""></mt-field>
      <mt-field label="" placeholder="" type="textarea" rows="5" v-model="projectIntroduce"></mt-field>
        <mt-button @click.stop.prevent="handleNewProject()" type="primary" size="large" style="
        text-align: center;
        font-weight: 600;
        margin-top:5px; border-radius: 0px;
        background-color:#59acff;"></mt-button>
      <!--<button ></button>-->
    </form>

  </div>
</template>
<style scoped>


  *{
    font-family: "PingFang SC";
    text-align: left;
    font-size: 0.5rem;
  }

</style>
<script>
  import axios from 'axios'
  import $ from 'jquery'
  import ElFormItem from "../../node_modules/element-ui/packages/form/src/form-item.vue";
  import MtField from "../../node_modules/mint-ui/packages/field/src/field.vue";
  import HomeSwiper from './home-components/home-swiper'
  import MtDatetimePicker from "../../node_modules/mint-ui/packages/datetime-picker/src/datetime-picker.vue";
  import MtCell from "../../node_modules/mint-ui/packages/cell/src/cell.vue";
  import MtButton from "../../node_modules/mint-ui/packages/button/src/button.vue";
  import MessageBox from 'mint-ui'
  import 'font-awesome/css/font-awesome.min.css'
  export default {
    components: {
      MtButton,
      MtCell,
      MtDatetimePicker,
      MtField,
      ElFormItem,
      HomeSwiper,

    },
    name:"newProject",
    data:function () {
      return {
        projectName:"",
        projectIntroduce:"",
        file:"",
        projectDate:[],
        minDate:new Date(),
        startDate:"",
        endDate:"",
        targetCost:"",
        startNode:null,
        info:"",
        startValue:" 2018-01-10",
        endValue:" 2018-02-21",
        fileInfo:"",
        popVisible:false,
        popupVisible: false,
        popInfo:"!"
      }
    },
    methods:{
      openPicker(flag) {
        if(flag===1) {
          this.$refs.startPicker.open();
        } else {
          if(this.startNode!==null) {
            this.minDate = this.startNode;
            this.$refs.endPicker.open();
          } else {
            this.popupVisible = true;
          }
        }
      },
      handleNewProject : function() {
        var that = this;
        this.projectDate[0] = this.startValue;
        this.projectDate[1] = this.endValue;
        var formData = new FormData($("#myform"));
        formData.append("uploadFile", this.file);
        formData.append("projectName",this.projectName);
        formData.append("projectIntroduce",this.projectIntroduce);
        formData.append("projectDate",this.projectDate);
        formData.append("projectTarget",this.targetCost);
        let config = {
          headers:{'Content-Type':'application/x-www-form-urlencoded'}
        };  //

        console.log(formData.get("projectDate"));
        axios.post('/project',formData,config).then(function (res) {
          if(res.data==='success!'){
            that.popInfo = "";
            that.popVisible = true;
            setTimeout(()=>{
              that.$router.push({name:'Home'});
            },1000)
          } else {
            alert(":");
          }
          console.log(res);
        }).catch(function(err){
          console.log("err:"+err);
        });
      },
      handleFile: function () {
        this.file = $("input[type='file']")[0].files[0]
        this.fileInfo = this.file.name.length>=10? this.file.name.split(".")[0].slice(0,5)+"...~."+this.file.name.split(".")[1]:this.file.name;
      },
      handleEndChange: function () {
        let date  = new Date(this.endDate);
        this.endValue = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
      },
      handleStartChange: function () {
        let date  = new Date(this.startDate);
        this.startNode = date;
        this.startValue = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
        this.endValue = " 2018-02-21"
        this.endDate = this.startNode;
      },
    },
    created:function () {
        var that = this;
        this.axios.get("/home").then((res) => {
        }).catch(function (err) {
          console.log("");
          that.$router.push({name: 'Login'});
        })
    },


    mounted: function(){
      var height = screen.height - $("#myform").css("height").split("px")[0] - 120;
      console.log(height);
      $(".formImg").css("height",height+"px");
      console.log(screen.width);
      console.log($("#myform > a.mint-cell.mint-field.specFile > div.mint-cell-wrapper > div.mint-cell-value > div.mint-field-other > span.fileInfo"));
      $("#myform > a.mint-cell.mint-field.specFile > div.mint-cell-wrapper > div.mint-cell-value > div.mint-field-other > span.fileInfo").css("right",screen.width-221+"px");
    }
  }
</script>
<style scoped>
  .center_wrapper{
    display: flex !important;
    /*flex-direction: row;*/
    /*align-items: center;*/
    justify-content: center !important;
    flex-direction: row-reverse !important;
  }
  .putCenter{
    color: white;
    font-weight: bold;
    font-size: 19px;
    text-align: center;
    border: none;
    border-radius: 50%;
    background-color: #28e289;
    height: 22px;
    box-shadow: 1px 1px 5px #d7d7d7;
    /*line-height: 22px;*/
    /*position: relative;*/
    /*top: -2px;*/
    /*position: relative;*/
    /*right: 6rem;*/
    width: 22px;
    /*line-height: 15px;*/
  }
  .setLeft{
    line-height:50px;
    font-size: 14px;
    color: dodgerblue;
    position: absolute;
    left: 120px;
  }
  .upLdFile{
    display: inline-block;
    color: white;
    font-weight: bold;
    font-size: 19px;
    text-align: center;
    border: none;
    border-radius: 50%;
    background-color: #a0ade1;
    height: 22px;
    /*position: relative;*/
    /*right: 6rem;*/
    width: 22px;
    line-height: 22px;
  }
  span.fileInfo{
    color: #707070;
    font-size: 16px;
    display: inline-block;
    position: relative;
    right: 0px;
    z-index: 1;
   }
  .formImg{
    background-image: url("../assets/form.jpg");
    width: 100%;
    height: 18vh;
    background-size: 100%;
    background-position: 0 20%;
  }
  #myform{
    margin-top: 0;
  }
  .formImg h1{
    height: 88px;
    line-height: 88px;
    font-size: 30px;
    padding-left: 20px;
    color: dodgerblue;
  }
  .popUp{
    font-family: "PingFang SC";
    background-color: rgba(0, 0, 0, 0.62);
    color: rgba(255, 255, 255, 0.63);
    width: 100% !important;
    height: 50px!important;
    line-height: 50px !important;
    text-align: center;
  }
  .plus{
    display: inline-block;
    position: relative;
    top: -2px;
    font-weight: 600;
    line-height: 22px;
    height: 25px;
    font-size: 24px;
    padding-bottom: 1px;
  }
  .center_wrapper{
    display: flex !important;
    /*flex-direction: row;*/
    /*align-items: center;*/
    justify-content: center !important;
    flex-direction: row-reverse !important;
  }

</style>




