<template>
  <header class="header clearfix">
    <nav class="header__navbar">
      <div class="header__navbar__icon">
        <i class="fa fa-navicon fa-1x" style="color:#ffffff;"></i>
      </div>
      <ul class="header__navbar__list"></ul>
    </nav>
    <div class="header__searchbar">
      <input type="text" class="header__searchbar__input" v-model="num" placeholder="ID" />
      <div class="header__searchbar__button">
        <i class="fa fa-search" @click="goDetail"></i>
      </div>
    </div>
    <div class="header__add">
      <router-link to="/newProject" class="router"><i class="fa fa-plus"></i></router-link>
    </div>
  </header>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'header',
  data: function(){
    return {
      num: null
    }
  },
  methods: {
    goDetail:function (num) {
      console.log(typeof this.num)
      var new_num = parseInt(this.num);
      if(isNaN(new_num)){
        this.$router.push({name: 'Detail', query: {projectID: 0}});
      } else {
        this.$router.push({name: 'Detail', query: {projectID: new_num}});
      }
    },
  }
}
</script>

<style lang="scss"  scoped>
.header{
  height: 60px;
  background: #34a1ff;
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  z-index: 999;
  /*border-bottom: 1px solid gainsboro;*/
  box-shadow: 0px 1px 6px rgba(89, 172, 255, 0.56);

  .header__navbar{
    float: left;
    width: 50px;

    .header__navbar__icon{
      width: 100%;
      text-align: center;
      line-height: 60px;
      font-size: 20px;
      color: #409eff;
    }
  }

  .header__searchbar{
    width: calc( 100% - 175px );
    float: left;
    height: 30px;
    margin: 10px 5px;
    text-align: left;
    /*border-left: 2px solid #409eff;*/
    padding: 5px 0px 5px 25px;

    .header__searchbar__input{
      width: calc( 100% - 60px );
      background: #ffffff;
      outline: none;
      border: 1.5px solid #ffffff;
      border-right: none;
      height: 100%;
      vertical-align: top;
      border-radius: 15px 0 0 15px;
      max-width: 180px;
      text-indent: 16px;
      font-size: 16px;
      line-height: 30px;
      overflow: hidden;
      float: left;
    }

    .header__searchbar__button{
      width: 30px;
      height: 100%;
      float: left;
      box-shadow: none;
      border: 1.5px solid #ffffff;
      border-left:none;
      border-radius: 0 15px 15px 0;
      background: #ffffff;
      font-size: 18px;
      line-height: 30px;
      color: #5eb4ff;
    }
  }

  .header__add{
    float: right;
    width: 50px;
    text-align: center;
    line-height: 60px;
    font-size: 20px;
    font-weight: lighter;
    color: #ffffff;
  }
  .router{
    color: #ffffff;
  }
}
</style>



