<template>
  <div id="app">
    <header-component ></header-component>
    <router-view></router-view>
    <footer-component :link-actived="linkActived" ></footer-component>
  </div>
</template>

<script>
import HeaderComponent from './components/common-components/header'
import FooterComponent from './components/common-components/footer'
//import { getCostList, ReactLogin, ReactSignUp, ReactUser } from './util/Mock'

export default {
  name: 'app',
  data: function(){
    return {
      newPath : this.$route.path,
      hasLogin: false
    }
  },
  computed: {
    linkActived: function(){
      return this.newPath
    }
  },
  methods: {
    freshPath: function(){
      this.newPath = this.$route.path;
    }
  },

  watch: {
    '$route': 'freshPath'
  },
  components: { HeaderComponent , FooterComponent }
}
</script>

<style>
  @font-face {
    font-family: "PingFang SC";
    src: url('./assets/PingFang-SC-Regular.eot');
  }
*{
  margin: 0;
  padding: 0;
}
.clearfix::after{
  display: block;
  clear: both;
  height: 0;
  content: '';
}
html{
  /*background: rgba(238, 242, 247, 0.68);*/
  /*background: #f2f2f2;*/
  background: #ffffff;
  margin-top: 60px;
  margin-bottom: 60px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



