<template>
<div>
  <div class="formImg">
    <h1></h1>
  </div>
  <mt-popup
    v-model="popVisible"
    position="top"
    :modal=false
    class="popUp"
  >
    <h2>{{prompt}}</h2>
  </mt-popup>
  <mt-cell title="ID" :value=projectID style="text-align: left;">
    <i slot="icon" class="fa fa-id-card-o cellIcon"></i>
  </mt-cell>
  <mt-cell title="" :value=starter style="text-align: left;">
    <i slot="icon" class="fa fa-send cellIcon"></i>
  </mt-cell>
  <mt-cell title="" :value=receiver style="text-align: left;">
    <i slot="icon" class="fa fa-hand-grab-o cellIcon"></i>
  </mt-cell>
  <mt-cell title="" :value=fortune-rangeValue style="text-align: left;">
    <i slot="icon" class="fa fa-dollar cellIcon" style="color: orangered"></i>
  </mt-cell>
  <mt-cell :label=valueInfo title="" style="text-align:left;">
    <mt-range  v-model="rangeValue" style="text-align:left;width: 18em" :max=fortune :step="100">
      <div slot="start">0</div>
      <div slot="end">{{fortune}}</div>
    </mt-range>
  </mt-cell>
  <mt-cell title="Cystal" :value="coinNumber" style="text-align: left;">
    <i slot="icon" class="fa fa-money cellIcon"></i>
  </mt-cell>
  <mt-field type="text" label="" placeholder="" v-model="code" style="text-align: left;"></mt-field>


  <mt-button type="primary" size="large" @click.prevent.stop="goTrade()" style="border-radius: 0"></mt-button>
</div>
</template>


<script>
import MtField from "../../node_modules/mint-ui/packages/field/src/field.vue";
import MtButton from "../../node_modules/mint-ui/packages/button/src/button.vue";

export default {
  title: 'purchase',
  components:{
    MtButton,
    MtField},
    data () {
    return {
      linkActived: '/purchase',
      code:"",
      receiver:"",
      starter:"",
      projectID:"",
      fortune:0,
      rangeValue:0,
      popVisible:false,
      prompt:"!"
    }
  },
  computed:{
    valueInfo: function () {
      return ""+this.rangeValue;
    },
    coinNumber: function () {
      return this.rangeValue/100;
    }
  },
  created:function () {
    var that = this;
    this.receiver = this.$route.query.ProjectReceiver;
    this.projectID = this.$route.query.ProjectID;
    this.axios.get('/getStarter').then(function (res) {
      console.log("starter:"+res.data[0].userId);
      that.starter = res.data[0].userId;
      that.fortune = parseInt(res.data[0].fortune);
      console.log(typeof that.fortune);
      console.log(res.data[0])
    }).catch(function (err) {
      console.error("something wrong!");
    })
  },
  methods:{
    goTrade:function () {
      var that = this;
      this.axios.post("/trade",{
        starter: that.starter,
        receiver: that.receiver,
        code: that.code,
        expenditure:that.rangeValue,
        projectId:that.projectID
      }).then(function (res) {
        console.log(res);
        if(res.data==="success") {
          that.prompt = "!";
          that.popVisible = true;
          setTimeout(()=>{
            that.$router.push({name:'Home'});
          },1000)
        } else {
          that.prompt = "!!!";
          that.popVisible = true;
        }
      }).catch(function (err) {
        console.log(err);
      })
    }
  }
}
</script>



<style scoped>
.header{
    padding-top:0.5rem;
    margin-bottom:-1rem;
}
  .clearfix:after{
    content: " ";
    display: block;
    clear: both;
    height: 0;
  }
  .clearfix {
    zoom: 1;
  }
.input{
    margin-top:30%;
    width:100%;
    height:2rem;
    line-height:2rem;
}
.btn{
    margin-top:40%;

    background:#ffb024;
    height:2rem;
    font-size:1.5rem;
}
  div{
      font-size: 12px;
  }
  [data-dpr="2"] div{
      font-size: 24px;
  }
  [data-dpr="3"] div{
      font-size: 36px;
  }

  .font14{
      font-size: 14px;
  }
  [data-dpr="2"] .font14{
      font-size: 28px;
  }
  [data-dpr="3"] .font14{
      font-size: 42px;
  }

  .font16{
    font-size: 16px;
  }
  [data-dpr="2"] .font16{
    font-size: 32px;
  }
  [data-dpr="3"] .font16{
    font-size: 48px;
  }
  .font18{
    font-size: 18px;
  }
  [data-dpr="2"] .font18{
    font-size: 36px;
  }
  [data-dpr="3"] .font18{
    font-size: 54px;
  }
  .font20{
    font-size: 20px;
  }
  [data-dpr="2"] .font20{
    font-size: 40px;
  }
  [data-dpr="3"] .font20{
    font-size: 60px;
  }
  .font32{
    font-size: 32px;
  }
  [data-dpr="2"] .font32{
    font-size: 64px;
  }
  [data-dpr="3"] .font32{
    font-size: 96px;
  }

  .font36{
    font-size: 36px;
  }
  [data-dpr="2"] .font36{
    font-size: 72px;
  }
  [data-dpr="3"] .font36{
    font-size: 108px;
  }
.formImg{
  background-color: #fafafa;
  background: url("../assets/house.png") no-repeat;
  width: 100%;
  height: 18vh;
  background-size: 77%;
  background-position: 122% -8%;
}
.formImg h1{
  padding-top: 20px;
  text-align: left;
  height: 88px;
  line-height: 88px;
  font-size: 30px;
  padding-left: 20px;
  color: dodgerblue;
}
  .cellIcon{
    width: 20px;
    color:dodgerblue;
    position:relative;
    top: 2px;
    height: 18.5px;
    line-height: 18.5px;
  }

  .popUp{
    background-color: rgba(0, 0, 0, 0.62);
    color: white;
    width: 100% !important;
    height: 50px!important;
    line-height: 50px !important;
  }
</style>



