<template xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div class="user">
    <div class="userPanel" >
    <div class="headImg"></div>
      <h2 class="name_Style">{{userName}}</h2>

<div class="userInfo">
    <mt-cell title="ID" :value="userID" class="mtInfo" style="text-align: left; min-height: 65px ">
      <i slot="icon" class="fa fa-user-circle-o cellIcon" style="color: dodgerblue; font-size: 22px;"></i>
    </mt-cell>
    <mt-cell title="" :value="userName" class="mtInfo" style="text-align: left; min-height: 65px ">
      <i slot="icon" class="fa fa-id-card cellIcon" style="color: #3faa37; font-size: 22px; "></i>
    </mt-cell>
    <mt-cell title="" class="mtInfo" style="text-align: left; min-height: 65px;">
      <i slot="icon" class="fa fa-money cellIcon" style="color: #ff6222; font-size: 22px; "></i>
      {{restMoney}}
      &nbsp; <router-link to="/charge"><span class="plus"><i class="fa fa-plus"></i></span></router-link>
    </mt-cell>
    <mt-cell title="" :value="projectEngage" class="mtInfo"  style="text-align: left; min-height: 65px ">
      <i slot="icon" class="fa fa-handshake-o cellIcon" style="color: #7d337a; font-size: 22px; "></i>
    </mt-cell>
    <mt-cell title="" :value="projectInitiate"  class="mtInfo" style="text-align: left; min-height: 65px ">
      <i slot="icon" class="fa fa-hand-grab-o cellIcon" style="color: #bf1400; font-size: 22px; "></i>

    </mt-cell>
    </div>
</div>
    <p class="join-project-tittle font32"></p>
        <list :parent="parent"></list>
  </div>
</template>

<script>
import FooterComponent from './common-components/footer'
import List from './common-components/list'
import $ from 'jquery'

export default {
  name: 'user',
  data () {
    return {
      linkActived: '/user',
      userName:"",
      projectEngage: "",
      restMoney:"",
      projectInitiate:"",
      parent:"User",
      project:[],
      userID:""
    }
  },
  created: function () {
    var that = this;
    this.axios.get("/user").then(function (res) {
      console.log(res);
      var data = res.data;
      that.userName = data.userName;
      that.projectEngage = data.userProject.length;
      that.restMoney = data.fortune;
      that.projectInitiate = data.projectInitiate;
      that.userID = data.userId;
      for(let i =0;i<that.projectEngage; i++){
        that.project.pop();
      }
      for(let i =0;i<that.projectEngage; i++){
        that.project.push(data.userProject[i]);
      }
      console.log(that.project)
    }).catch(function (err) {
      if(err=="Error: Request failed with status code 404"||err=="Error: Request failed with status code 504"){
        console.log("");
        that.$router.push({name: 'Login'});
      }
      console.log("err"+err);
    });

    this.axios.get("/engageList").then(function (res) {
      console.log(res);
    }).catch(function (err) {
      console.log("err"+err);
    });

    $(".mint-cell-wrapper").css("backgroundImage","none");


  },

  methods: {
    query: function(){
      // let userName
    },

  },
  components: { FooterComponent, List}
}
</script>

