    text-align:left;
}



  div{
      font-size: 12px;
  }
  [data-dpr="2"] div{
      font-size: 24px;
  }
  [data-dpr="3"] div{
      font-size: 36px;
  }

  .font14{
      font-size: 14px;
  }
  [data-dpr="2"] .font14{
      font-size: 28px;
  }
  [data-dpr="3"] .font14{
      font-size: 42px;
  }

  .font16{
    font-size: 16px;
  }
  [data-dpr="2"] .font16{
    font-size: 32px;
  }
  [data-dpr="3"] .font16{
    font-size: 48px;
  }
  .font18{
    font-size: 18px;
  }
  [data-dpr="2"] .font18{
    font-size: 36px;
  }
  [data-dpr="3"] .font18{
    font-size: 54px;
  }
  .font20{
    font-size: 20px;
  }
  [data-dpr="2"] .font20{
    font-size: 40px;
  }
  [data-dpr="3"] .font20{
    font-size: 60px;
  }
  .font32{
    font-size: 32px;
  }
  [data-dpr="2"] .font32{
    font-size: 64px;
  }
  [data-dpr="3"] .font32{
    font-size: 96px;
  }

  .font36{
    font-size: 36px;
  }
  [data-dpr="2"] .font36{
    font-size: 72px;
  }
  [data-dpr="3"] .font36{
    font-size: 108px;
  }

  p.more{
    text-align: center;
    width: 40px;
    border-radius: 3px;
    box-shadow: 0px 0px 3px grey;
  }
  p.more:hover{
    background-color: #bdeeff;
  }

  .headImg{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: url("../assets/user.png") no-repeat;
    /*background: url("../assets/demo-user.png") no-repeat;*/
    background-size:70%;
    /*background-position: 52% 55%;*/
    background-position: 52% 0%;
    background-color: #fff;
    margin: 0px auto;
    border: 3px solid #ffffff;
    box-shadow: 0px 0px 5px #8c8c8c;
  }

  .name_Style{
    margin-top: 8px;
    margin-bottom: 35px;
    color: #ffffff;
    font-family: "Century Gothic","PingFang SC","Microsoft JhengHei UI";
