    /*background-color:rgb(52,161,255); */
    overflow: hidden;
    padding-top: 35px;
    margin: 0 auto;
    /*border-radius: 10px;*/
    /*margin-top: 20px;*/
    /*box-shadow: 0 0 8px #c0c0c0;*/
  }

  .mtInfo{
    font-family: "PingFang SC";

  }

  span.plus{
    display: inline-block;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #47b6ff;
    box-shadow: 0px 0px 3px #d2d2d2;
  }

  span.plus i{
    text-align: center;
    display: inline-block;
    width: 25px;
    margin: 0 auto;
    color: white;
    line-height: 25px;
  }


</style>



