<template>
<div class="cashroute">

  <div class="formImg">
    <h1></h1>
  </div>
<!--<ul class="detail-class">-->
<!--<li class='font16'></li>-->
<!--<li class='font16'>50</li>-->
<!--<li class='font16'>2017715</li>-->
<!--<li class='font16'></li>-->
<!--<li class='font16'></li>-->
<!--</ul>-->

<!--<ul class="detail-class">-->
