<!--<li class='font16'></li>-->
<!--</ul>-->

  <ul v-for="item in items" class="detail-class">
    <li class='font16'>{{item.RouteName}}</li>
    <li class='font16'>{{item.cost}}</li>
    <li class='font16'>{{item.RouteDate}}</li>
    <li class='font16'>{{item.starter}}</li>
    <li class='font16'>{{item.receiver}}</li>
  </ul>

  <!--<h2 class="title02"></h2>-->
  <mt-button type="primary" size="large" @click.prevent.stop="goCashRoute()" style="
  width: 90%; margin: 0 auto; position: relative; top: -10px; box-shadow: 2px 2px 5px #d3d3d3"></mt-button>


</div>
</template>

<script>
  export default {
    title:"cashaRoute",
    components:{

    },
    data:function () {
      return {
        items:[],
         projectID:""
      }
    },
    methods:{
      goCashRoute: function () {
        this.$router.push({name: 'newCashRoute',query:{projectID: this.projectID}});
      }
    },
    created: function () {
      var that = this;
      this.projectID = this.$route.query.projectID;
      if(this.projectID===undefined){
        that.$router.push({name: 'Home'});
        return;
      }
      this.axios.get('/getRoute?projectID='+this.projectID).then(function (res) {
        console.log(res);
        for(var i=0;i < that.items.length; i++){
          that.items.pop();
        }
        for(i=0; i<res.data.length; i++){
          that.items.push(res.data[i]);
        };
      }).catch(function (err) {
        console.log(err.response.data);
        that.$router.push({name: 'Login'});
      })
    }

  }
</script>


