<template>
  <div class="newProject">
    <div class="formImg">
      <h1></h1>
    </div>
    <mt-popup
      v-model="popVisible"
      position="top"
      :modal=false
      class="popUp"
    >{{popInfo}}</mt-popup>
    <form>
      <mt-field type="text" name="targetCost" v-model="targetCost" label="" placeholder=""></mt-field>
      <mt-field type="text" v-model="userPassword" label="" placeholder=""></mt-field>
      <mt-button @click.stop.prevent="handleCharge()" type="primary" size="large" style="
        text-align: center;
        font-weight: 600;
        margin-top:5px; border-radius: 0px;
        background-color:#59acff;"></mt-button>
    </form>

  </div>
</template>
<style scoped>


  *{
    font-family: "PingFang SC";
    text-align: left;
    font-size: 0.5rem;
  }

</style>
<script>
  import axios from 'axios'
  import $ from 'jquery'
  import ElFormItem from "../../node_modules/element-ui/packages/form/src/form-item.vue";
  import MtField from "../../node_modules/mint-ui/packages/field/src/field.vue";
  import HomeSwiper from './home-components/home-swiper'
  import MtDatetimePicker from "../../node_modules/mint-ui/packages/datetime-picker/src/datetime-picker.vue";
  import MtCell from "../../node_modules/mint-ui/packages/cell/src/cell.vue";
  import MtButton from "../../node_modules/mint-ui/packages/button/src/button.vue";
  import MessageBox from 'mint-ui'
  import 'font-awesome/css/font-awesome.min.css'
  export default {
    components: {
      MtButton,
      MtCell,
      MtDatetimePicker,
      MtField,
      ElFormItem,
      HomeSwiper,

    },
    name:"newProject",
    data:function () {
      return {
        projectName:"",
        projectIntroduce:"",
        userPassword: "",
        file:"",
        projectDate:[],
        minDate:new Date(),
        startDate:"",
        endDate:"",
        targetCost: null,
        startNode:null,
        popVisible:false,
        popupVisible: false,
        popInfo:"!"
      }
    },
    methods:{
      handleCharge() {
        var that = this;
        this.axios.post("/charge",{
          chargeMoney:  this.targetCost,
          userPassword: this.userPassword
        }).then(function(res){
          console.log(res);
          if(res.data==="success"){
            that.popInfo = "!";
            that.popVisible = true;
            setTimeout(function () {
              that.$router.push({name: 'User'});
            },1000);
          }
        }).catch(function (err) {
          that.popInfo = "!";
          that.popVisible = true;
          setTimeout(function () {
            that.popupVisible = false;
          },1000);
        });
      }
    },
    created:function () {

    },


    mounted: function(){
    }
  }
</script>
<style scoped>
  .center_wrapper{
    display: flex !important;
    /*flex-direction: row;*/
    /*align-items: center;*/
    justify-content: center !important;
    flex-direction: row-reverse !important;
  }
  .putCenter{
    color: white;
    font-weight: bold;
    font-size: 19px;
    text-align: center;
    border: none;
    border-radius: 50%;
    background-color: #28e289;
    height: 22px;
    box-shadow: 1px 1px 5px #d7d7d7;
    /*line-height: 22px;*/
    /*position: relative;*/
    /*top: -2px;*/
    /*position: relative;*/
    /*right: 6rem;*/
    width: 22px;
    /*line-height: 15px;*/
  }
  .setLeft{
    line-height:50px;
    font-size: 14px;
    color: dodgerblue;
    position: absolute;
    left: 120px;
  }
  .upLdFile{
    display: inline-block;
    color: white;
    font-weight: bold;
    font-size: 19px;
    text-align: center;
    border: none;
    border-radius: 50%;
    background-color: #a0ade1;
    height: 22px;
    /*position: relative;*/
    /*right: 6rem;*/
    width: 22px;
    line-height: 22px;
  }
  span.fileInfo{
    color: #707070;
    font-size: 16px;
    display: inline-block;
    position: relative;
    right: 0px;
    z-index: 1;
  }
  .formImg{
    background-image: url("../assets/form.jpg");
    width: 100%;
    height: 18vh;
    background-size: 100%;
    background-position: 0 20%;
  }
  #myform{
    margin-top: 0;
  }
  .formImg h1{
    height: 88px;
    line-height: 88px;
    font-size: 30px;
    padding-left: 20px;
    color: dodgerblue;
  }
  .popUp{
    font-family: "PingFang SC";
    background-color: rgba(0, 0, 0, 0.62);
    color: rgba(255, 255, 255, 0.63);
    width: 100% !important;
    height: 50px!important;
    line-height: 50px !important;
    text-align: center;
  }
  .plus{
    display: inline-block;
    position: relative;
    top: -2px;
    font-weight: 600;
    line-height: 22px;
    height: 25px;
    font-size: 24px;
    padding-bottom: 1px;
  }
  .center_wrapper{
    display: flex !important;
    /*flex-direction: row;*/
    /*align-items: center;*/
    justify-content: center !important;
    flex-direction: row-reverse !important;
  }

</style>




